<template>
  <li :class="{'list-item': true, border: hasDivider}">
    <span  :class="{disabled: value.x, success: value.o}">
      {{value.id}}
    </span>
    <button
      class="x"
      v-if="!value.x"
      @click="disable()"
    >
      X
    </button>
    <button
      class="o"
      v-if="!value.o && !value.x"
      @click="success()"
    >
      O
    </button>
    <button
      class="n"
      v-if="!value.o && !value.x"
      @click="timestamp()">
      N
    </button>
    <div v-if="!value.x && !value.o" :style="counter" class="box">
      {{nCounter}}
    </div>
  </li>
</template>

<script>

export default {
  props: [
    'value',
    'dbKey',
    'name',
    'index',
    'divider',
  ],
  computed: {
    nCounter() {
      if (this.value.n) {
        return Object.values(this.value.n).length;
      }
      return '';
    },
    counter() {
      const n = this.nCounter;
      if (n) {
        return {
          width: `${n * 10}px`,
        };
      }
      return '';
    },
    hasDivider() {
      return (this.index + 1) % this.divider === 0;
    },
  },
  methods: {
    success() {
      this.$store.dispatch('update', {
        name: this.name,
        key: this.dbKey,
        data: { o: true },
      });
    },
    disable() {
      this.$store.dispatch('update', {
        name: this.name,
        key: this.dbKey,
        data: { x: true },
      });
    },
    timestamp() {
      this.$store.dispatch('updateTime', {
        name: this.name,
        key: this.dbKey,
        data: Date.now(),
      });
    },
  },
};
</script>
